.strikethrough {
  text-decoration: line-through;
}

.item-container {
  margin: 0 1rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-top: 1px solid darkgrey;
  padding: 1rem 0;
  cursor: pointer;
}

.item-container:hover {
  background-color: #fafafa;
}

.item-content {
  margin: 0 1rem;
  width: 100%;
  display: flex;
  align-items: center;
}

.action-container {
  margin: 0 1rem;
  height: 2rem;
  display: flex;
  visibility: hidden;
}

.action-container.visible {
  visibility: visible;
}

.action-button {
  margin-left: 1rem;
  border: 0;
  background-color: transparent;
  font-size: 1rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 1rem;
  width: 2rem;
}

.action-button:hover {
  background-color: #f0f0f0;
}

.text-input {
  margin: 0;
  height: 100%;
  min-width: 100%;
  font-size: 1rem;
  font-family: 'Comic Sans MS', serif;
  border: none;
  overflow-x: scroll;
}

.done-checkbox {
  margin-right: 0.5rem;
}
